body {
  font-size: 16px;
  font-size: 1rem;
  font-family: 'proxima-nova', sans-serif;
  font-weight: 400;
  line-height: 1.4;
  background-color: #fff;
  position: relative; }

/*
*
* Grid Layout
*/
.container {
  width: 100%;
  margin: 0 auto; }

.row {
  display: flex;
  padding-left: 1.5rem;
  padding-right: 1.5rem; }

.col-4 {
  width: 33.3333%; }

.col-6 {
  width: 50%; }

.col-8 {
  width: 66.3333%; }

.col-12 {
  width: 100%; }

.container:before,
.container:after,
.row:before,
.row:after {
  content: " ";
  display: table; }

.container:after,
.row:after {
  clear: both; }

.space-between {
  justify-content: space-between; }

/*
*
* Typography
*/
h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: 'proxima-nova-condensed';
  line-height: 1.2; }

p {
  display: inline-block;
  margin-top: 0;
  margin-bottom: 1rem; }

/*
*
* Elements
*/
img,
.img-responsive {
  max-width: 100%;
  display: inline-block; }

.mb-2 {
  margin-bottom: 2rem; }

.site-header {
  height: 3rem;
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.08);
  position: sticky;
  top: 0;
  background-color: #fff;
  z-index: 8; }

.site-logo,
.site-nav {
  display: flex;
  align-items: center;
  height: 3rem; }

.site-logo img {
  height: 2rem;
  width: 2rem;
  border-radius: 50%; }

.site-nav {
  float: right; }
  .site-nav img {
    height: 1.125rem; }

@media (min-width: 768px) {
  .site-header {
    width: 34%; }
  .site-content {
    width: 66%; }
  .site-content,
  .site-footer {
    float: left;
    margin-left: 34%; }
  .site-header {
    height: 100vh;
    background-color: #efefef;
    position: fixed; } }

.mobile-menu {
  width: 100%;
  height: 100vh;
  position: fixed;
  z-index: 10;
  top: 0;
  left: -100%;
  background-color: #fff;
  overflow: hidden;
  transition: 0.5s; }
  .mobile-menu header {
    width: 100%;
    height: 3rem;
    display: flex;
    justify-content: flex-end;
    align-items: center; }
    .mobile-menu header img {
      height: 1.25rem;
      margin-right: 1rem;
      padding: 0.675rem;
      border-radius: 50%; }
  .mobile-menu .nav {
    margin: 0;
    padding: 0 1.5rem;
    list-style: none;
    width: 100%;
    height: calc( 100vh - 6rem);
    display: flex;
    justify-content: center;
    flex-direction: column; }
    .mobile-menu .nav li {
      width: 100%;
      display: flex; }
    .mobile-menu .nav a {
      font-size: 2rem;
      color: #757575;
      text-decoration: none;
      font-weight: 700; }
  .mobile-menu footer {
    padding-left: 1.5rem;
    padding-right: 1.5rem;
    height: 3rem; }

.hello {
  font-size: 3rem;
  font-weight: 700;
  color: #212121;
  margin-bottom: 1.5rem;
  margin-top: 5rem; }

.description {
  font-size: 1.25rem;
  line-height: 1.6;
  position: relative; }
  .description span {
    font-weight: 600;
    border-bottom: 0.125rem solid #212121; }
    .description span:hover .info {
      visibility: visible; }
    .description span .info {
      visibility: hidden;
      font-size: 1rem;
      font-weight: normal;
      border-bottom: 0;
      padding: 1rem;
      width: 15rem;
      height: 12rem;
      background-color: #efefef;
      position: absolute;
      top: -2rem;
      left: calc(44% - 7.5rem);
      margin-left: 0;
      box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.08);
      z-index: 4;
      display: flex;
      align-items: center; }

.heading {
  color: #212121;
  font-size: 1.25rem;
  line-height: 1.2;
  text-transform: uppercase;
  font-weight: 400;
  position: relative;
  margin-top: 6rem; }
  .heading:before {
    content: "";
    width: 1.5rem;
    height: 0.175rem;
    background-color: #212121;
    position: absolute;
    z-index: 6;
    top: -1.25rem;
    left: 0; }

.recent-work {
  margin-bottom: 3rem;
  position: relative; }
  .recent-work h1 {
    font-size: 1.5rem;
    line-height: 1.2;
    margin-bottom: 0.5rem; }
  .recent-work p {
    color: #757575;
    margin-bottom: 0; }
  .recent-work .link-overlay {
    background-color: transparent;
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    z-index: 2; }

.works .recent-work:nth-child(1) {
  color: #0984e3; }

.works .recent-work:nth-child(2) {
  color: #6c5ce7; }

.works .recent-work:nth-child(3) {
  color: #fd79a8; }

.site-footer {
  margin-top: 3rem; }

.copyright {
  border-top: solid 1px #d1d1d1;
  padding-top: 1rem;
  width: 100%;
  display: inline-block;
  color: #212121;
  line-height: 1; }

.contact-me {
  background-color: #212121;
  height: 2.5rem;
  width: 2.5rem;
  border-radius: 50%;
  position: fixed;
  bottom: 1.5rem;
  right: 1.5rem;
  cursor: pointer; }
  .contact-me a {
    height: 100%;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center; }
  .contact-me img {
    height: 1.125rem;
    width: auto;
    margin-left: -0.125rem; }
