.mobile-menu {
  width: 100%;
  height: 100vh;
  position: fixed;
  z-index: 10;
  top: 0;
  left: -100%;
  background-color: #fff;
  overflow: hidden;
  transition: 0.5s;
  header {
    width: 100%;
    height: 3rem;
    display: flex;
    justify-content: flex-end;
    align-items: center;

    img {
      height: 1.25rem;
      margin-right: 1rem;
      padding: 0.675rem;
      border-radius: 50%;
    }
  }

  .nav {
    margin: 0;
    padding: 0 1.5rem;
    list-style: none;
    width: 100%;
    height: calc( 100vh - 6rem);
    display: flex;
    justify-content: center;
    flex-direction: column;

    li {
      width: 100%;
      display: flex;

    }
    a {
      font-size: 2rem;
      color: $menu;
      text-decoration: none;
      font-weight: 700;
    }
  }

  footer {
    padding-left: 1.5rem;
    padding-right: 1.5rem;
    height: 3rem;
  }
}